
const apis = {'/docs/course/oop-notes': () => import('@/obj/docs/course/oop-notes.json'),
'/docs/course/dp': () => import('@/obj/docs/course/dp.json'),
'/docs/course/alg-notes': () => import('@/obj/docs/course/alg-notes.json')}
const navTags = [{"name": "OOP", "cnt": 1, "to": "/tags/oop"}, {"name": "c++", "cnt": 1, "to": "/tags/c++"}, {"name": "algorithm", "cnt": 2, "to": "/tags/algorithm"}, {"name": "dynamic programming", "cnt": 1, "to": "/tags/dynamic programming"}, {"name": "notes", "cnt": 1, "to": "/tags/notes"}]
const navCategories = [{"name": "course", "children": [{"name": "OOP Notes", "to": "/docs/course/oop-notes"}, {"name": "动态规划笔记", "to": "/docs/course/dp"}, {"name": "算法笔记", "to": "/docs/course/alg-notes"}]}]

function get(url) {
  if (apis[url]) return apis[url]();
  else return Promise.reject(new Error('404'));
}
export default { get, navTags, navCategories }

